<template>
  <SearchBar @search="handleSearch" :limitDays="30">
    <template #icon>
      <v-icon>mdi-heart-pulse</v-icon>
    </template>
    <template #title>
      生理監控
    </template>
    <template #dashboardTypes>
      <select class="select">
        <option>疲勞監控與身體組成分析</option>
      </select>
    </template>
  </SearchBar>

  <section class="pa-4" v-if="responseTired?.length > 0 && responseBody?.length > 0">
    <v-row>
      <v-col cols="6">
        <div class="box bg-white" v-if="responseTired.length > 0">
          <h2>疲勞監控</h2>
          <tiredContainer />
          <tiredChart />
        </div>
        <NoData v-else :message="queryNoDataText" />
      </v-col>
      <v-col cols="6">
        <div class="box bg-white" v-if="responseBody.length > 0">
          <h2>身體組成監控 </h2>
          <bodyContainer />
          <bodyChart />
        </div>
        <NoData v-else :message="queryNoDataText" />
      </v-col>
    </v-row>
  </section>
  <NoData v-else :message="queryNoDataText" />

</template>

<script setup>
import SearchBar from '../components/searchBar.vue'
import tiredContainer from './components/tiredContainer.vue'
import tiredChart from './components/tiredChart.vue'
import bodyContainer from './components/bodyContainer.vue'
import bodyChart from './components/bodyChart.vue'
import NoData from '../components/noData.vue'
import { dashboard } from '@/requests'

const selectedUserId = ref('')
const responseTired = ref({})
const responseBody = ref({})
const queryNoDataText = ref('')

provide('responseTired', responseTired)
provide('responseBody', responseBody)

const fetchData = async (queryString) => {
  try {
    // 獲取疲勞監控數據
    const tiredResponse = await dashboard.getPhysiologyIndicatorTired(selectedUserId.value, queryString)
    responseTired.value = tiredResponse.data || {}

    // 獲取身體組成數據
    const bodyResponse = await dashboard.getPhysiologyIndicatorBody(selectedUserId.value, queryString)
    responseBody.value = bodyResponse.data || {}
  } catch (error) {
    console.error('獲取數據失敗:', error)
    responseTired.value = {}
    responseBody.value = {}
  }
}

const handleSearch = async (searchParams) => {
  const queryString = searchParams.queryString
  selectedUserId.value = searchParams.selectedUserId
  await fetchData(queryString)

  if (responseTired.value?.length === 0 || responseBody.value?.length === 0) {
    queryNoDataText.value = `${searchParams.selectedUserName}在所選日期範圍內${searchParams.sDate}~${searchParams.eDate}無資料。`
  } else {
    queryNoDataText.value = ``
  }
}


</script>
