<template>
  <div class="body-indicators">
    <indicatorBlock title="體重" :data="weightData" :color="CHART_COLORS[0]" />
    <indicatorBlock title="體脂率" :data="bodyFatData" :color="CHART_COLORS[1]" />
    <indicatorBlock title="總水份" :data="totalWaterData" :color="CHART_COLORS[2]" />
    <indicatorBlock title="去脂重" :data="leanMassData" :color="CHART_COLORS[3]" />
  </div>
</template>

<script setup>
import indicatorBlock from './indicatorBlock.vue'

const responseBody = inject('responseBody', ref([]))

// 提取各指標數據
const weightData = computed(() => {
  return responseBody.value.map(item => item['體重']).filter(val => val != null)
})

const bodyFatData = computed(() => {
  return responseBody.value.map(item => item['體脂率']).filter(val => val != null)
})

const totalWaterData = computed(() => {
  return responseBody.value.map(item => item['水份重']).filter(val => val != null)
})

const leanMassData = computed(() => {
  return responseBody.value.map(item => item['去脂重']).filter(val => val != null)
})
</script>

<style scoped>
.body-indicators {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 4px;
  margin-bottom: 36px;
}
</style>