<template>
  <h3>歷次檢測</h3>
  <div ref="chartRef" style="width: 100%; height: 400px;"></div>
</template>

<script setup>
import * as echarts from 'echarts'
import 'echarts/lib/component/title'
import 'echarts/lib/component/graphic'

const responseTired = inject('responseTired')
const chartRef = ref(null)
let chartInstance = null

const hasData = computed(() => {
  return responseTired.value && responseTired.value.length > 0
})

const chartOption = computed(() => {
  if (!hasData.value) return null

  const dates = responseTired.value.map(d => d.date)
  const hbData = responseTired.value.map(d => d.hb)
  const bunData = responseTired.value.map(d => d.bun)
  const ckData = responseTired.value.map(d => d.ck)

  return {
    tooltip: {
      trigger: 'axis',
      axisPointer: {
        type: 'cross'
      }
    },
    legend: {
      data: ['Hb', 'BUN', 'CK'],
      top: 0
    },
    grid: {
      left: '3%',
      right: '4%',
      bottom: '10%',
      containLabel: true
    },
    xAxis: {
      type: 'category',
      data: dates,
      axisLabel: {
        rotate: 45
      }
    },
    yAxis: [
      {
        type: 'value',
        name: 'Hb/BUN',
        position: 'left'
      },
      {
        type: 'value',
        name: 'CK',
        position: 'right'
      }
    ],
    dataZoom: [
      {
        type: 'slider',
        show: true,
        xAxisIndex: [0],
        start: 0,
        end: 100
      }
    ],
    series: [
      {
        name: 'Hb',
        type: 'line',
        data: hbData,
        smooth: true,
        itemStyle: {
          color: '#ff6b9d'
        },
        lineStyle: {
          color: '#ff6b9d'
        }
      },
      {
        name: 'BUN',
        type: 'line',
        data: bunData,
        smooth: true,
        itemStyle: {
          color: '#3b82f6'
        },
        lineStyle: {
          color: '#3b82f6'
        }
      },
      {
        name: 'CK',
        type: 'bar',
        data: ckData,
        yAxisIndex: 1,
        itemStyle: {
          color: '#a855f7'
        }
      }
    ]
  }
})

const showNoData = () => {
  if (chartInstance) {
    chartInstance.clear()
    chartInstance.setOption({
      title: {
        text: '暫無資料',
        left: 'center',
        top: 'middle',
        textStyle: {
          color: '#888',
          fontSize: 22
        }
      },
      graphic: [
        {
          type: 'text',
          left: 'center',
          top: '55%',
          style: {
            text: '請選擇條件後查詢',
            fill: '#aaa',
            font: '16px sans-serif'
          }
        }
      ]
    })
  }
}

const initChart = () => {
  if (!chartRef.value) return

  if (chartInstance) {
    chartInstance.dispose()
  }

  chartInstance = echarts.init(chartRef.value)
  if (hasData.value && chartOption.value) {
    chartInstance.setOption(chartOption.value)
  } else {
    showNoData()
  }
}

onMounted(async () => {
  await nextTick()
  initChart()
})

watch(chartOption, () => {
  if (chartInstance && chartOption.value && hasData.value) {
    chartInstance.setOption(chartOption.value)
  } else {
    showNoData()
  }
})

watch(() => responseTired.value, async () => {
  await nextTick()
  initChart()
}, { deep: true })
</script>
