<template>
  <div>
    <h3>組成變化追蹤</h3>
    <div class="indicator-buttons">
      <v-btn v-for="indicator in indicators" :key="indicator.key"
        :color="selectedIndicators.includes(indicator.key) ? 'primary' : 'default'"
        :variant="selectedIndicators.includes(indicator.key) ? 'flat' : 'outlined'" size="small"
        @click="toggleIndicator(indicator.key)" class="ma-1">
        {{ indicator.label }}
      </v-btn>
    </div>

    <!-- ECharts 圖表 -->
    <div ref="chartContainer" class="chart-container"></div>
  </div>
</template>

<script setup>
import * as echarts from 'echarts'

const responseBody = inject('responseBody', ref([]))
const chartContainer = ref(null)
let chartInstance = null

const indicators = [
  { key: '體重', label: '體重' },
  { key: '體脂率', label: '體脂率' },
  { key: '肌肉量', label: '肌肉量' },
  { key: '脂肪重', label: '脂肪重' },
  { key: '水份比例', label: '水份比例' },
  { key: '水份重', label: '水份重' }
]

const selectedIndicators = ref(['體重', '體脂率'])

// 計算水份比例
const processedData = computed(() => {
  return responseBody.value.map(item => ({
    ...item,
    '水份比例': item['體重'] && item['水份重']
      ? ((1 - (item['體重'] - item['水份重']) / item['體重']) * 100).toFixed(1)
      : 0
  }))
})

const toggleIndicator = (indicator) => {
  const index = selectedIndicators.value.indexOf(indicator)
  if (index > -1) {
    selectedIndicators.value.splice(index, 1)
  } else if (selectedIndicators.value.length < 2) {
    selectedIndicators.value.push(indicator)
  }
}

const initChart = () => {
  if (!chartContainer.value || processedData.value.length === 0) return

  if (chartInstance) {
    chartInstance.dispose()
  }

  chartInstance = echarts.init(chartContainer.value)

  const dates = processedData.value.map(item => item.date)

  const series = selectedIndicators.value.map((indicator, index) => {
    const data = processedData.value.map(item => parseFloat(item[indicator]) || 0)

    return {
      name: indicator,
      type: 'line',
      yAxisIndex: getYAxisIndex(indicator),
      data: data,
      lineStyle: {
        color: CHART_COLORS[index % CHART_COLORS.length]
      },
      itemStyle: {
        color: CHART_COLORS[index % CHART_COLORS.length]
      }
    }
  })

  const option = {
    tooltip: {
      trigger: 'axis',
      axisPointer: {
        type: 'cross'
      }
    },
    legend: {
      data: selectedIndicators.value,
      top: 0
    },
    grid: {
      left: '3%',
      right: '4%',
      bottom: '10%',
      containLabel: true
    },
    xAxis: {
      type: 'category',
      data: dates,
      axisLabel: {
        rotate: 45
      }
    },
    yAxis: [
      {
        type: 'value',
        name: '重量 (kg)',
        position: 'left'
      },
      {
        type: 'value',
        name: '比例 (%)',
        position: 'right'
      }
    ],
    dataZoom: [
      {
        type: 'slider',
        show: true,
        xAxisIndex: [0],
        start: 0,
        end: 100
      }
    ],
    series: series
  }

  chartInstance.setOption(option)
}

const getYAxisIndex = (indicator) => {
  // 比例類指標使用右軸，重量類指標使用左軸
  const percentageIndicators = ['體脂率', '水份比例']
  return percentageIndicators.includes(indicator) ? 1 : 0
}

watch([selectedIndicators, processedData], () => {
  nextTick(() => {
    initChart()
  })
}, { deep: true })

onMounted(() => {
  nextTick(() => {
    initChart()
  })
})

onUnmounted(() => {
  if (chartInstance) {
    chartInstance.dispose()
  }
})
</script>

<style scoped>
.indicator-buttons {
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 12px;
  ;
}

.chart-container {
  width: 100%;
  height: 400px;
}
</style>